export default {
	data() {
		return {
			tabCur: 1, //当前标签
			swiperCur: 0, //当前轮播index
			categoryList: [{}, {}, {}, {}, {}, {}, {}, {}],
			cardList: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}]
		}
	},
	methods: {
		tabSelect(e) { //标签选择
			this.tabCur = e.currentTarget.dataset.id;
			this.swiperCur = this.tabCur
		},
		swiperChange(e) { //轮播变化
			this.swiperCur = e.detail.current;
			this.tabCur = this.swiperCur
		}
	}
}
